.chat {
  flex: 0.65;
  display: flex;
  flex-direction: column;
}

.chat__header {
  padding: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-bottom: 1px solid lightgray;
}

.chat__headerInfo {
  flex:1;
  padding-left: 25px;
}

.chat__headerInfo > h3 {
  margin-bottom: 3px;
  font-weight: 500;
}

.chat__headerInfo > p {
  color: gray;
}

.chat__headerRight {
  display: flex;
  flex-direction: row;
  min-width: 100px;
}

.chat__body {
    flex:1;
  background-image: url("https://i.imgur.com/TnNwdvV.png");
  background-repeat: repeat;
  background-position: center;
  padding: 30px;
  overflow:scroll;
}

.chat__message {
    position: relative;
    font-size: 16px;
    padding:10px;
    background-color: #fff;
    border-radius: 10px;
    width: fit-content;
    margin-bottom: 30px;
}

.chat__name {
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size:xx-small;
  
}

.chat__timestamp {
    margin-left: 10px;
    font-size: xx-small;
}

.chat__receiver {
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat__footer{

  display: flex;
  height: 62px;
  flex-direction: row;
  justify-content: space-between;
  border: 1px solid lightgray;
  align-items: center;



}

.chat__footer > form {
  flex:1;
  display: flex;

}
.chat__footer > form > input {

  flex:1;
  border-radius: 30px;
  padding: 10px;
  border: none;
  
   

}

.chat__footer > form > button {
  display: none
}

.chat__footer > .MuiSvgIcon-root {
  padding: 10px;
  color: gray;
}
